.nav{
  width: 100%;
  display: flex;
  justify-content: space-around;
  box-shadow: 0px 0px 16px -7px black;

}

.lnav{
  width: 5cm;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.nav .lnav ul li{
  /* text-decoration: none; */
  list-style: none;
display: none;
}

.rnav{
  width: 18cm;
}
.rnav a{
  text-decoration: none;
  color: rgb(22, 24, 23);
}
.rnav ul{
  display: flex;
  justify-content: space-around;

}
.rnav ul li{
  /* text-decoration: none; */
  list-style: none;
  margin-top: 5px;
 

}
.rnav ul li:hover{
color: rgb(155, 9, 9);


}
