@media only screen  and (max-width:800px) {
.nav{
    flex-direction: column;
    padding-top:10px;
    padding-bottom: 10px;
}      
        .nav .rnav ul{
          flex-direction: column;
          
          }
        
          .nav .lnav{
              width: 100%;
           justify-content: space-between;
           padding-left: 15px;
           padding-right: 15px;            }
        .nav .rnav{
            width: 100%;
   
        }
            .nav .lnav ul li{
               display: block;
               position: relative;
               top: 8px;
               
              }
            .nav .rnav ul  li{
                text-align: center;
               /* border: 1px solid red; */
               margin-left: -30px;
               border-bottom: 1px solid black;
               padding-top: 8px;
               padding-bottom: 8px;
            }
            .nav .rnav{
                border-top: 1px solid rgb(59, 57, 57);
            }
           
            /* Transform Translate basics.. */
           .dnone{
             display: none;
           }
           
  
}